//Menu wrapper
.menu-list {
    width: 224px;
    
    //Menu items
    .menu-item {
        margin-left: 0;
    }
}

.menu-propper {
    padding-bottom: 8px;
}

.menu-item {
    width: 100%;
    font-weight: 500;
    justify-content: flex-start;
    border-radius: 0;
    padding: 11px 16px;
    line-height: 1.8rem;

    &:hover {
        background-color: rgba(22, 24, 35, 0.03);
    }
}

//Header
.header {
    position: relative;
    height: 50px;
    margin-top: -8px;
}

.back-btn {
    width: 50px;
    height: 100%;
    background-color: transparent;
    cursor: pointer;
}

.header-title {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

